import React, { Fragment } from "react";
import { Box, Typography } from "@mui/material";
import { HashLink } from "react-router-hash-link";

function WhyChooseUsHover() {
  const scrollToTop = (el) => {
    const yCoordinate = el.getBoundingClientRect().top + window.pageYOffset;
    const yOffset = -100;
    window.scrollTo({ top: yCoordinate + yOffset, behavior: "smooth" });
  };
  return (
    <Fragment>
      <Box
        sx={{
          backgroundColor: "white",
          minHeight: { xs: "20vh", md: "25vh", lg: "30vh" },
          maxHeight: { xs: "60vh", md: "75vh" },
          overflowX: "hidden",
          width: { xs: "55vw", sm: "45vw", md: "45vw" },
          borderRadius: "12px",
          boxShadow: "5px 5px 0px 0px grey",
          display: "flex",
          flexDirection: "column",
          gap: 3,
          pt: 3,
          pl: 2,
          pb: 2,
        }}
      >
        <HashLink
          to="/why-choose-us#irtcots"
          scroll={scrollToTop}
          style={{ textDecoration: "none", color: "inherit" }}
        >
          <Typography
            sx={{
              color: "primary.dark",
              fontSize: { xs: "16px", sm: "18px", md: "20px" },
              fontWeight: "800",
            }}
          >
            Irresistible Reasons to choose our Training Services
          </Typography>
        </HashLink>
        <HashLink
          to="/why-choose-us#training-methodology"
          scroll={scrollToTop}
          style={{ textDecoration: "none", color: "inherit" }}
        >
          <Typography
            sx={{
              color: "primary.dark",
              fontSize: { xs: "16px", sm: "18px", md: "20px" },
              fontWeight: "800",
            }}
          >
            Our Training Methodology
          </Typography>
        </HashLink>
        <HashLink
          scroll={scrollToTop}
          to="/why-choose-us#irtcoss"
          style={{ textDecoration: "none", color: "inherit" }}
        >
          <Typography
            sx={{
              color: "primary.dark",
              fontSize: { xs: "16px", sm: "18px", md: "20px" },
              fontWeight: "800",
            }}
          >
            Irresistible Reasons to choose our staffing Services
          </Typography>
        </HashLink>

        <HashLink
          to="/why-choose-us#milestones-achievements"
          scroll={scrollToTop}
          style={{ textDecoration: "none", color: "inherit" }}
        >
          <Typography
            sx={{
              color: "primary.dark",
              fontSize: { xs: "16px", sm: "18px", md: "20px" },
              fontWeight: "800",
            }}
          >
            A Sneak Peak Into Our Achievements
          </Typography>
        </HashLink>
      </Box>
    </Fragment>
  );
}

export default WhyChooseUsHover;
